@import url("https://fonts.googleapis.com/css2?family=EB+Garamond:ital,wght@1,600&display=swap");
body {
  font-family: sans-serif;
  background-color: pink;
}
.nav-hero {
  display: block;
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  padding-bottom: 0.5rem;
  color: black;
  text-align: center;
  background-color: crimson;
  padding-inline-start: 0px;
  border-radius: 0.2rem 0.5rem;
}
.output {
  display: block;
  margin-top: 4rem;
  margin-left: auto;
  margin-right: auto;
  border: 1px solid black;
  width: 59.75%;
  padding-bottom: 1rem;
  padding-top: 1rem;
  text-align: right;
  color: blue;
  font-size: large;
  font-weight: bold;
  background-color: white;
}
table {
  width: 60%;
  margin-left: auto;
  margin-right: auto;
  border: 1px solid black;
  background-color: darkgray;
}
.section {
  width: 100%;
  max-width: 600px;
  min-height: 200px;
  margin: auto;
  padding: 2rem 1rem;
  text-align: center;
}

tr {
  padding: 1rem;
}

th {
  padding-top: 1rem;
  padding-bottom: 1rem;
  cursor: pointer;
}
.symbol-left {
  color: blue;
}
.sym-bottom {
  color: blue;
}
.equalb {
  background-color: crimson;
}
footer {
  padding: 0.5rem;
  background-color: crimson;
  border-radius: 0.5rem 0.2rem;
  justify-content: center;
  position: absolute;
  bottom: 0px;
  left: 0px;
  width: 100%;
}

.list-no-bullet {
  list-style-type: none;
}
.footer-bottom {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding-inline-start: 0px;
}
a {
  color: black;
  padding-right: 1rem;
}
